:root {
  --dimension: 25vmin;
}

.cell {
  height: var(--dimension);
  width: var(--dimension);
  float: left;

  display: flex;
  align-items: center;
  justify-content: center;
}

.cell img {
  width: 60%;
}

.table {
  position: relative;
  justify-self: center;
  background-image: url("./resources/grid.svg");
  background-repeat: no-repeat, repeat;
  background-position: center;
  background-size: contain;
  height: calc(3 * var(--dimension));
  width: calc(3 * var(--dimension));
}

.status {
  font-family: "Permanent Marker", cursive;
  font-size: 3em;
}
.separator {
  height: 10%;
  background-color: green;
}

.R1 {
  position: absolute;
  top: 17%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1.5);
  width: calc(2.3 * var(--dimension));
}

.R2 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1.5);
  width: calc(2.3 * var(--dimension));
}

.R3 {
  position: absolute;
  top: 83%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1.5);
  width: calc(2.3 * var(--dimension));
}

.C1 {
  position: absolute;
  top: 50%;
  left: 17%;
  transform: translate(-50%, -50%) scale(1.5) rotate(90deg);
  width: calc(2.3 * var(--dimension));
}

.C2 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1.5) rotate(90deg);
  width: calc(2.3 * var(--dimension));
}

.C3 {
  position: absolute;
  top: 50%;
  left: 83%;
  transform: translate(-50%, -50%) scale(1.5) rotate(90deg);
  width: calc(2.3 * var(--dimension));
}

.D1 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1.5) rotate(45deg);
  width: calc(3 * var(--dimension));
}

.D2 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1.5) rotate(-45deg);
  width: calc(3 * var(--dimension));
}

@media only screen and (max-width: 480px) and (orientation: portrait) {
  .table {
    transform: translate(0%, -30%);
  }
}
